'use client'

import React, { useRef, useEffect, useState, useCallback } from 'react'

interface SelectionBox {
  startX: number
  startY: number
  width: number
  height: number
  id: string
  translatedText?: string
  isTranslating?: boolean
}

interface ImageTranslatorProps {
  imageFile: File
  onTranslationComplete: (translatedImageUrl: string) => void
  onCancel: () => void
}

export default function ImageTranslator({ imageFile, onTranslationComplete, onCancel }: ImageTranslatorProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [image, setImage] = useState<HTMLImageElement | null>(null)
  const [isDrawing, setIsDrawing] = useState(false)
  const [currentSelection, setCurrentSelection] = useState<SelectionBox | null>(null)
  const [selections, setSelections] = useState<SelectionBox[]>([])
  const [isTranslating, setIsTranslating] = useState(false)
  const [scale, setScale] = useState({ x: 1, y: 1 })
  const [startPoint, setStartPoint] = useState<{ x: number; y: number } | null>(null)
  const [hoveredSelection, setHoveredSelection] = useState<string | null>(null)
  const [lastTranslatedImageUrl, setLastTranslatedImageUrl] = useState<string>('')

  // Ïù¥ÎØ∏ÏßÄ Î°úÎìú
  useEffect(() => {
    if (!imageFile) return
    
    const img = new Image()
    img.onload = () => {
      setImage(img)
      const canvas = canvasRef.current
      if (canvas) {
        // Ïª®ÌÖåÏù¥ÎÑà ÎÑàÎπÑÏóê ÎßûÍ≤å Ï∫îÎ≤ÑÏä§ ÌÅ¨Í∏∞ ÏÑ§Ï†ï
        const container = canvas.parentElement
        if (container) {
          const containerWidth = container.clientWidth
          const imgRatio = img.width / img.height
          
          // Í∞ÄÎ°úÎ•º Ïª®ÌÖåÏù¥ÎÑà ÎÑàÎπÑÏóê ÎßûÏ∂îÍ≥† ÎπÑÏú®Ïóê Îî∞Îùº ÏÑ∏Î°ú Í≥ÑÏÇ∞
          const displayWidth = containerWidth
          const displayHeight = containerWidth / imgRatio
          
          // Ï∫îÎ≤ÑÏä§ ÌÅ¨Í∏∞Î•º ÌôîÎ©¥ ÌëúÏãú ÌÅ¨Í∏∞Î°ú ÏÑ§Ï†ï (Ï¢åÌëú ÏùºÏπòÎ•º ÏúÑÌï¥)
          canvas.width = displayWidth
          canvas.height = displayHeight
          canvas.style.width = `${displayWidth}px`
          canvas.style.height = `${displayHeight}px`
          
          // Ïä§ÏºÄÏùº ÎπÑÏú® Í≥ÑÏÇ∞: ÌôîÎ©¥ ÌÅ¨Í∏∞ ‚Üí ÏõêÎ≥∏ Ïù¥ÎØ∏ÏßÄ ÌÅ¨Í∏∞
          setScale({
            x: img.width / displayWidth,   // ÌôîÎ©¥ Ï¢åÌëúÎ•º ÏõêÎ≥∏ Ï¢åÌëúÎ°ú Î≥ÄÌôòÌïòÎäî ÎπÑÏú®
            y: img.height / displayHeight
          })
          
          // Ï∫îÎ≤ÑÏä§Ïóê Ïù¥ÎØ∏ÏßÄ Í∑∏Î¶¨Í∏∞ (ÌëúÏãú ÌÅ¨Í∏∞Ïóê ÎßûÏ∂§)
          const ctx = canvas.getContext('2d')
          if (ctx) {
            ctx.drawImage(img, 0, 0, displayWidth, displayHeight)
          }
        }
      }
    }
    
    const url = URL.createObjectURL(imageFile)
    img.src = url
    
    return () => URL.revokeObjectURL(url)
  }, [imageFile])

  // Ï∫îÎ≤ÑÏä§ Í∑∏Î¶¨Í∏∞
  const drawCanvas = useCallback(() => {
    const canvas = canvasRef.current
    const ctx = canvas?.getContext('2d')
    if (!canvas || !ctx || !image) return

    // Î∞∞Í≤Ω ÌÅ¥Î¶¨Ïñ¥
    ctx.clearRect(0, 0, canvas.width, canvas.height)
    
    // Ïù¥ÎØ∏ÏßÄ Í∑∏Î¶¨Í∏∞ (ÌôîÎ©¥ ÌÅ¨Í∏∞Ïóê ÎßûÏ∂§)
    ctx.drawImage(image, 0, 0, canvas.width, canvas.height)
    
    // Í∏∞Ï°¥ Î≤àÏó≠Îêú ÏòÅÏó≠Îì§ Í∑∏Î¶¨Í∏∞ (ÌôîÎ©¥ Ï¢åÌëúÍ≥ÑÏóêÏÑú ÏßÅÏ†ë Í∑∏Î¶¨Í∏∞)
    selections.forEach(selection => {
      if (selection.translatedText) {
        // ÏÑ†ÌÉù ÏòÅÏó≠ÏùÄ ÌôîÎ©¥ Ï¢åÌëúÍ≥Ñ Í∏∞Ï§ÄÏù¥ÎØÄÎ°ú ÏßÅÏ†ë ÏÇ¨Ïö©
        const boxX = selection.startX
        const boxY = selection.startY
        const boxWidth = selection.width
        const boxHeight = selection.height
        
        // Î∞òÌà¨Î™Ö Í≤ÄÏùÄÏÉâ Î∞∞Í≤Ω (85% Ìà¨Î™ÖÎèÑ)
        ctx.fillStyle = 'rgba(0, 0, 0, 0.85)'
        ctx.fillRect(boxX, boxY, boxWidth, boxHeight)
        
        // Í≤ΩÍ≥ÑÏÑ† (Ìò∏Î≤ÑÎêú ÏòÅÏó≠ÏùÄ Îπ®Í∞ÑÏÉâÏúºÎ°ú ÌëúÏãú)
        if (hoveredSelection === selection.id) {
          ctx.strokeStyle = '#ff0000'
        } else {
          ctx.strokeStyle = '#333333'
        }
        ctx.lineWidth = 1
        ctx.strokeRect(boxX, boxY, boxWidth, boxHeight)
        
        // X Î™®Ïñë ÏÇ≠Ï†ú ÌëúÏãú (Ìò∏Î≤ÑÎêú ÏòÅÏó≠ÏóêÎßå)
        if (hoveredSelection === selection.id) {
          ctx.strokeStyle = '#ff6666'
          ctx.lineWidth = 2
          const margin = Math.min(boxWidth, boxHeight) * 0.1
          
          // X Í∑∏Î¶¨Í∏∞
          ctx.beginPath()
          ctx.moveTo(boxX + margin, boxY + margin)
          ctx.lineTo(boxX + boxWidth - margin, boxY + boxHeight - margin)
          ctx.stroke()
          
          ctx.beginPath()
          ctx.moveTo(boxX + boxWidth - margin, boxY + margin)
          ctx.lineTo(boxX + margin, boxY + boxHeight - margin)
          ctx.stroke()
        }
        
        // Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶àÎ•º ÏòÅÏó≠Ïóê ÎßûÍ≤å ÎèôÏ†Å Í≥ÑÏÇ∞
        const maxTextWidth = boxWidth * 0.9
        const maxTextHeight = boxHeight * 0.9
        
        // ÌÖçÏä§Ìä∏ Ï§ÑÎ∞îÍøà Ï≤òÎ¶¨
        const words = selection.translatedText.split(' ')
        
        // Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶àÎ•º Ï∞æÍ∏∞ ÏúÑÌïú Ïù¥ÏßÑ ÌÉêÏÉâ
        let fontSize = Math.min(boxHeight * 0.3, 40) // ÏãúÏûë Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶à
        const minFontSize = 8
        const maxFontSize = Math.min(boxHeight * 0.5, 60)
        let bestFontSize = minFontSize
        
        // ÏµúÏ†Å Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶à Ï∞æÍ∏∞
        for (let testSize = minFontSize; testSize <= maxFontSize; testSize += 2) {
          ctx.font = `bold ${testSize}px Arial, sans-serif`
          
          // ÌÖçÏä§Ìä∏ Ï§ÑÎ∞îÍøà ÌÖåÏä§Ìä∏
          const lines: string[] = []
          let currentLine = ''
          
          for (const word of words) {
            const testLine = currentLine ? `${currentLine} ${word}` : word
            const testWidth = ctx.measureText(testLine).width
            
            if (testWidth <= maxTextWidth) {
              currentLine = testLine
            } else {
              if (currentLine) {
                lines.push(currentLine)
                currentLine = word
              } else {
                lines.push(word)
              }
            }
          }
          if (currentLine) lines.push(currentLine)
          
          // Ï¥ù ÎÜíÏù¥ Í≥ÑÏÇ∞
          const lineHeight = testSize * 1.3
          const totalHeight = lines.length * lineHeight
          
          // ÌÖçÏä§Ìä∏Í∞Ä ÏòÅÏó≠Ïóê Îì§Ïñ¥Í∞ÄÎäîÏßÄ ÌôïÏù∏
          if (totalHeight <= maxTextHeight) {
            bestFontSize = testSize
          } else {
            break
          }
        }
        
        // ÏµúÏ¢Ö Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶à Ï†ÅÏö©
        fontSize = Math.max(bestFontSize, minFontSize)
        ctx.font = `bold ${fontSize}px Arial, sans-serif`
        ctx.fillStyle = 'white'  // ÌÖçÏä§Ìä∏ ÏÉâÏÉÅÏùÑ Ìù∞ÏÉâÏúºÎ°ú Î≥ÄÍ≤Ω
        ctx.textAlign = 'center'
        ctx.textBaseline = 'top'
        
        // ÏµúÏ¢Ö ÌÖçÏä§Ìä∏ Ï§ÑÎ∞îÍøà
        const lines: string[] = []
        let currentLine = ''
        
        for (const word of words) {
          const testLine = currentLine ? `${currentLine} ${word}` : word
          const testWidth = ctx.measureText(testLine).width
          
          if (testWidth <= maxTextWidth) {
            currentLine = testLine
          } else {
            if (currentLine) {
              lines.push(currentLine)
              currentLine = word
            } else {
              lines.push(word)
            }
          }
        }
        if (currentLine) lines.push(currentLine)
        
        // Îã§Ï§ë ÎùºÏù∏ ÌÖçÏä§Ìä∏ Í∑∏Î¶¨Í∏∞
        const lineHeight = fontSize * 1.3
        const totalTextHeight = lines.length * lineHeight
        const startY = boxY + (boxHeight - totalTextHeight) / 2
        
        lines.forEach((line, index) => {
          const lineY = startY + index * lineHeight
          const lineX = boxX + boxWidth / 2
          ctx.fillText(line, lineX, lineY)
        })
      } else if (selection.isTranslating) {
        // Î≤àÏó≠ Ï§ë ÌëúÏãú (ÌôîÎ©¥ Ï¢åÌëúÍ≥ÑÏóêÏÑú ÏßÅÏ†ë Í∑∏Î¶¨Í∏∞)
        ctx.fillStyle = 'rgba(255, 255, 0, 0.3)'
        ctx.fillRect(selection.startX, selection.startY, selection.width, selection.height)
        
        ctx.strokeStyle = '#ffd700'
        ctx.lineWidth = 2
        ctx.strokeRect(selection.startX, selection.startY, selection.width, selection.height)
        
        ctx.fillStyle = 'white'  // Î≤àÏó≠ Ï§ë ÌÖçÏä§Ìä∏ÎèÑ Ìù∞ÏÉâÏúºÎ°ú Î≥ÄÍ≤Ω
        ctx.font = 'bold 16px Arial'
        ctx.textAlign = 'center'
        ctx.textBaseline = 'middle'
        ctx.fillText('Î≤àÏó≠ Ï§ë...', selection.startX + selection.width / 2, selection.startY + selection.height / 2)
      }
    })
    
    // ÌòÑÏû¨ ÏÑ†ÌÉù Ï§ëÏù∏ ÏòÅÏó≠ Í∑∏Î¶¨Í∏∞ (ÌôîÎ©¥ Ï¢åÌëúÍ≥ÑÏóêÏÑú ÏßÅÏ†ë Í∑∏Î¶¨Í∏∞)
    if (currentSelection) {
      ctx.strokeStyle = '#ff0000'
      ctx.lineWidth = 2
      ctx.setLineDash([5, 5])
      ctx.strokeRect(currentSelection.startX, currentSelection.startY, currentSelection.width, currentSelection.height)
      ctx.setLineDash([])
    }
  }, [image, selections, currentSelection, scale, hoveredSelection])

  // Ï∫îÎ≤ÑÏä§ ÏóÖÎç∞Ïù¥Ìä∏
  useEffect(() => {
    drawCanvas()
  }, [drawCanvas])

  // ÎßàÏö∞Ïä§ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨Îì§
  const handleMouseDown = (e: React.MouseEvent<HTMLCanvasElement>) => {
    if (!image) return
    
    const canvas = canvasRef.current
    if (!canvas) return
    
    const rect = canvas.getBoundingClientRect()
    // ÌôîÎ©¥ Ï¢åÌëúÎ•º Ï∫îÎ≤ÑÏä§ Ï¢åÌëúÎ°ú Î≥ÄÌôò (Ï∫îÎ≤ÑÏä§ÏôÄ ÌôîÎ©¥Ïù¥ ÎèôÏùºÌïú ÌÅ¨Í∏∞Ïù¥ÎØÄÎ°ú ÏßÅÏ†ë ÏÇ¨Ïö©)
    const x = e.clientX - rect.left
    const y = e.clientY - rect.top
    
    // Í∏∞Ï°¥ ÏÑ†ÌÉù ÏòÅÏó≠ ÌÅ¥Î¶≠ ÌôïÏù∏ (Ïó≠ÏàúÏúºÎ°ú Ï≤¥ÌÅ¨ÌïòÏó¨ ÏµúÏã† ÏòÅÏó≠ Ïö∞ÏÑ†)
    for (let i = selections.length - 1; i >= 0; i--) {
      const selection = selections[i]
      if (x >= selection.startX && x <= selection.startX + selection.width &&
          y >= selection.startY && y <= selection.startY + selection.height) {
        // Í∏∞Ï°¥ ÏòÅÏó≠ ÌÅ¥Î¶≠ Ïãú ÏÇ≠Ï†ú
        setSelections(prev => prev.filter(sel => sel.id !== selection.id))
        return
      }
    }
    
    // ÏÉàÎ°úÏö¥ ÏòÅÏó≠ ÏÑ†ÌÉù ÏãúÏûë
    setIsDrawing(true)
    setStartPoint({ x, y })
    setCurrentSelection({
      id: `selection-${Date.now()}`,
      startX: x,
      startY: y,
      width: 0,
      height: 0,
      isTranslating: false,
      translatedText: ''
    })
  }

  const handleMouseMove = (e: React.MouseEvent<HTMLCanvasElement>) => {
    const canvas = canvasRef.current
    if (!canvas) return
    
    const rect = canvas.getBoundingClientRect()
    // ÌôîÎ©¥ Ï¢åÌëúÎ•º Ï∫îÎ≤ÑÏä§ Ï¢åÌëúÎ°ú Î≥ÄÌôò (Ï∫îÎ≤ÑÏä§ÏôÄ ÌôîÎ©¥Ïù¥ ÎèôÏùºÌïú ÌÅ¨Í∏∞Ïù¥ÎØÄÎ°ú ÏßÅÏ†ë ÏÇ¨Ïö©)
    const x = e.clientX - rect.left
    const y = e.clientY - rect.top
    
    // Ìò∏Î≤ÑÎêú ÏÑ†ÌÉù ÏòÅÏó≠ Ï∞æÍ∏∞
    let hoveredId: string | null = null
    for (let i = selections.length - 1; i >= 0; i--) {
      const selection = selections[i]
      if (x >= selection.startX && x <= selection.startX + selection.width &&
          y >= selection.startY && y <= selection.startY + selection.height) {
        hoveredId = selection.id
        break
      }
    }
    setHoveredSelection(hoveredId)
    
    // ÎìúÎûòÍ∑∏ Ï§ëÏù∏ Í≤ΩÏö∞
    if (isDrawing && startPoint && image) {
      const currentX = x
      const currentY = y
      
      const width = currentX - startPoint.x
      const height = currentY - startPoint.y
      
      setCurrentSelection(prev => prev ? {
        ...prev,
        width: Math.abs(width),
        height: Math.abs(height),
        startX: width < 0 ? currentX : startPoint.x,
        startY: height < 0 ? currentY : startPoint.y
      } : null)
      
      // Ï∫îÎ≤ÑÏä§ Îã§Ïãú Í∑∏Î¶¨Í∏∞
      drawCanvas()
    }
  }

  // Ïö∞ÌÅ¥Î¶≠ Ïª®ÌÖçÏä§Ìä∏ Î©îÎâ¥
  const handleContextMenu = (e: React.MouseEvent<HTMLCanvasElement>) => {
    e.preventDefault()
    
    if (!image) return
    
    const canvas = canvasRef.current
    if (!canvas) return
    
    const rect = canvas.getBoundingClientRect()
    // ÌôîÎ©¥ Ï¢åÌëúÎ•º Ï∫îÎ≤ÑÏä§ Ï¢åÌëúÎ°ú Î≥ÄÌôò (Ï∫îÎ≤ÑÏä§ÏôÄ ÌôîÎ©¥Ïù¥ ÎèôÏùºÌïú ÌÅ¨Í∏∞Ïù¥ÎØÄÎ°ú ÏßÅÏ†ë ÏÇ¨Ïö©)
    const x = e.clientX - rect.left
    const y = e.clientY - rect.top
    
    // Ïö∞ÌÅ¥Î¶≠Ìïú ÏúÑÏπòÏùò ÏÑ†ÌÉù ÏòÅÏó≠ Ï∞æÍ∏∞
    for (let i = selections.length - 1; i >= 0; i--) {
      const selection = selections[i]
      if (x >= selection.startX && x <= selection.startX + selection.width &&
          y >= selection.startY && y <= selection.startY + selection.height) {
        // Ìï¥Îãπ ÏòÅÏó≠ ÏÇ≠Ï†ú
        setSelections(prev => prev.filter(sel => sel.id !== selection.id))
        return
      }
    }
  }

  const handleMouseUp = async () => {
    if (!isDrawing || !currentSelection) return

    setIsDrawing(false)

    // ÎÑàÎ¨¥ ÏûëÏùÄ ÏÑ†ÌÉù ÏòÅÏó≠ÏùÄ Î¨¥Ïãú
    if (Math.abs(currentSelection.width) < 20 || Math.abs(currentSelection.height) < 20) {
      setCurrentSelection(null)
      return
    }

    // ÏùåÏàò ÌÅ¨Í∏∞ Ï†ïÍ∑úÌôî
    const normalizedSelection = {
      ...currentSelection,
      startX: currentSelection.width < 0 ? currentSelection.startX + currentSelection.width : currentSelection.startX,
      startY: currentSelection.height < 0 ? currentSelection.startY + currentSelection.height : currentSelection.startY,
      width: Math.abs(currentSelection.width),
      height: Math.abs(currentSelection.height),
      isTranslating: true
    }

    // ÏÑ†ÌÉù ÏòÅÏó≠ÏùÑ Î™©Î°ùÏóê Ï∂îÍ∞Ä
    setSelections(prev => [...prev, normalizedSelection])
    setCurrentSelection(null)

    // OCR Î∞è Î≤àÏó≠ ÏàòÌñâ
    await translateSelectedArea(normalizedSelection)
  }

  // ÏÑ†ÌÉùÎêú ÏòÅÏó≠ Î≤àÏó≠
  const translateSelectedArea = async (selection: SelectionBox) => {
    try {
      // ÏÑ†ÌÉùÎêú ÏòÅÏó≠Ïùò Ïù¥ÎØ∏ÏßÄ Îç∞Ïù¥ÌÑ∞ Ï∂îÏ∂ú
      const canvas = document.createElement('canvas')
      const ctx = canvas.getContext('2d')
      if (!ctx || !image) return

      // ÌôîÎ©¥ Ï¢åÌëúÎ•º ÏõêÎ≥∏ Ïù¥ÎØ∏ÏßÄ Ï¢åÌëúÎ°ú Î≥ÄÌôò
      const originalX = selection.startX * scale.x
      const originalY = selection.startY * scale.y
      const originalWidth = selection.width * scale.x
      const originalHeight = selection.height * scale.y

      canvas.width = originalWidth
      canvas.height = originalHeight

      // ÏÑ†ÌÉùÎêú ÏòÅÏó≠Îßå Ï∫îÎ≤ÑÏä§Ïóê Í∑∏Î¶¨Í∏∞ (ÏõêÎ≥∏ Ïù¥ÎØ∏ÏßÄÏóêÏÑú Ìï¥Îãπ ÏòÅÏó≠ Ï∂îÏ∂ú)
      ctx.drawImage(
        image,
        originalX, originalY, originalWidth, originalHeight,
        0, 0, originalWidth, originalHeight
      )

      // Ï∫îÎ≤ÑÏä§Î•º BlobÏúºÎ°ú Î≥ÄÌôò
      const blob = await new Promise<Blob>((resolve) => {
        canvas.toBlob((blob) => resolve(blob!), 'image/png')
      })

      // FormDataÎ°ú API Ìò∏Ï∂ú
      const formData = new FormData()
      formData.append('image', blob, 'selection.png')
      formData.append('ocrOnly', 'true')

      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData
      })

      const result = await response.json()
      
      if (result.success && result.translatedText) {
        // Î≤àÏó≠ ÏôÑÎ£å - ÏÑ†ÌÉù ÏòÅÏó≠ ÏóÖÎç∞Ïù¥Ìä∏
        setSelections(prev => prev.map(sel => 
          sel.id === selection.id 
            ? { ...sel, translatedText: result.translatedText, isTranslating: false }
            : sel
        ))
      } else {
        // Î≤àÏó≠ Ïã§Ìå® - ÏÑ†ÌÉù ÏòÅÏó≠ Ï†úÍ±∞
        setSelections(prev => prev.filter(sel => sel.id !== selection.id))
      }
    } catch (error) {
      console.error('Translation error:', error)
      setSelections(prev => prev.filter(sel => sel.id !== selection.id))
    }
  }

  // Î≤àÏó≠Îêú ÏòÅÏó≠Ïù¥ Î≥ÄÍ≤ΩÎê† ÎïåÎßàÎã§ ÏûêÎèôÏúºÎ°ú Î≤àÏó≠Îêú Ïù¥ÎØ∏ÏßÄ ÏÉùÏÑ±
  useEffect(() => {
    const autoGenerateTranslatedImage = async () => {
      // Î≤àÏó≠Îêú ÏòÅÏó≠Ïù¥ ÏûàÍ≥†, Î™®Îì† Î≤àÏó≠Ïù¥ ÏôÑÎ£åÎêú Í≤ΩÏö∞ÏóêÎßå Ïã§Ìñâ
      const translatedSelections = selections.filter(sel => sel.translatedText && !sel.isTranslating)
      if (translatedSelections.length === 0) return
      
      // Î≤àÏó≠ Ï§ëÏù∏ ÏòÅÏó≠Ïù¥ ÏûàÏúºÎ©¥ ÎåÄÍ∏∞
      if (selections.some(sel => sel.isTranslating)) return

      // ÏûêÎèôÏúºÎ°ú Î≤àÏó≠Îêú Ïù¥ÎØ∏ÏßÄ ÏÉùÏÑ±
      try {
        const translatedImageUrl = await generateTranslatedImage()
        if (translatedImageUrl && translatedImageUrl !== lastTranslatedImageUrl) {
          setLastTranslatedImageUrl(translatedImageUrl)
          // ÏÉÅÏúÑ Ïª¥Ìè¨ÎÑåÌä∏Ïóê ÏûêÎèôÏúºÎ°ú Ï†ÑÎã¨
          onTranslationComplete(translatedImageUrl)
        }
      } catch (error) {
        console.error('Auto translation generation error:', error)
      }
    }

    autoGenerateTranslatedImage()
  }, [selections, onTranslationComplete, lastTranslatedImageUrl])

  // Î≤àÏó≠Îêú Ïù¥ÎØ∏ÏßÄ ÏÉùÏÑ± Ìï®Ïàò (handleCompleteÏóêÏÑú Î∂ÑÎ¶¨)
  const generateTranslatedImage = async (): Promise<string | null> => {
    if (selections.length === 0) return null
    if (selections.some(sel => sel.isTranslating)) return null

    try {
      const canvas = document.createElement('canvas')
      const ctx = canvas.getContext('2d')
      if (!ctx || !image) return null

      canvas.width = image.width
      canvas.height = image.height

      // ÏõêÎ≥∏ Ïù¥ÎØ∏ÏßÄ Í∑∏Î¶¨Í∏∞
      ctx.drawImage(image, 0, 0)

      // Î≤àÏó≠Îêú ÌÖçÏä§Ìä∏Îì§ Í∑∏Î¶¨Í∏∞
      selections.forEach(selection => {
        if (selection.translatedText) {
          // ÌôîÎ©¥ Ï¢åÌëúÎ•º ÏõêÎ≥∏ Ïù¥ÎØ∏ÏßÄ Ï¢åÌëúÎ°ú Î≥ÄÌôò
          const realX = selection.startX * scale.x
          const realY = selection.startY * scale.y
          const realWidth = selection.width * scale.x
          const realHeight = selection.height * scale.y
          
          // Î∞òÌà¨Î™Ö Í≤ÄÏùÄÏÉâ Î∞∞Í≤Ω (70% Ìà¨Î™ÖÎèÑ)
          ctx.fillStyle = 'rgba(0, 0, 0, 0.7)'
          ctx.fillRect(realX, realY, realWidth, realHeight)
          
          // Í≤ΩÍ≥ÑÏÑ†
          ctx.strokeStyle = '#333333'
          ctx.lineWidth = 1
          ctx.strokeRect(realX, realY, realWidth, realHeight)
          
          // Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶àÎ•º ÏòÅÏó≠Ïóê ÎßûÍ≤å ÎèôÏ†Å Í≥ÑÏÇ∞
          const maxTextWidth = realWidth * 0.9
          const maxTextHeight = realHeight * 0.9
          
          // ÌÖçÏä§Ìä∏ Ï§ÑÎ∞îÍøà Ï≤òÎ¶¨
          const words = selection.translatedText.split(' ')
          
          // Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶àÎ•º Ï∞æÍ∏∞ ÏúÑÌïú Í≥ÑÏÇ∞
          const minFontSize = 8
          const maxFontSize = Math.min(realHeight * 0.5, 60)
          let bestFontSize = minFontSize
          
          // ÏµúÏ†Å Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶à Ï∞æÍ∏∞
          for (let testSize = minFontSize; testSize <= maxFontSize; testSize += 2) {
            ctx.font = `bold ${testSize}px Arial, sans-serif`
            
            // ÌÖçÏä§Ìä∏ Ï§ÑÎ∞îÍøà ÌÖåÏä§Ìä∏
            const lines: string[] = []
            let currentLine = ''
            
            for (const word of words) {
              const testLine = currentLine ? `${currentLine} ${word}` : word
              const testWidth = ctx.measureText(testLine).width
              
              if (testWidth <= maxTextWidth) {
                currentLine = testLine
              } else {
                if (currentLine) {
                  lines.push(currentLine)
                  currentLine = word
                } else {
                  lines.push(word)
                }
              }
            }
            if (currentLine) lines.push(currentLine)
            
            // Ï¥ù ÎÜíÏù¥ Í≥ÑÏÇ∞
            const lineHeight = testSize * 1.3
            const totalHeight = lines.length * lineHeight
            
            // ÌÖçÏä§Ìä∏Í∞Ä ÏòÅÏó≠Ïóê Îì§Ïñ¥Í∞ÄÎäîÏßÄ ÌôïÏù∏
            if (totalHeight <= maxTextHeight) {
              bestFontSize = testSize
            } else {
              break
            }
          }
          
          // ÏµúÏ¢Ö Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶à Ï†ÅÏö©
          const fontSize = Math.max(bestFontSize, minFontSize)
          ctx.font = `bold ${fontSize}px Arial, sans-serif`
          ctx.fillStyle = 'white'
          ctx.textAlign = 'center'
          ctx.textBaseline = 'top'
          
          const centerX = realX + realWidth / 2
          
          // ÏµúÏ¢Ö ÌÖçÏä§Ìä∏ Ï§ÑÎ∞îÍøà
          const lines: string[] = []
          let currentLine = ''
          
          for (const word of words) {
            const testLine = currentLine ? `${currentLine} ${word}` : word
            const testWidth = ctx.measureText(testLine).width
            
            if (testWidth <= maxTextWidth) {
              currentLine = testLine
            } else {
              if (currentLine) {
                lines.push(currentLine)
                currentLine = word
              } else {
                lines.push(word)
              }
            }
          }
          if (currentLine) lines.push(currentLine)
          
          const lineHeight = fontSize * 1.3
          const totalTextHeight = lines.length * lineHeight
          const startY = realY + (realHeight - totalTextHeight) / 2
          
          lines.forEach((line, index) => {
            const lineY = startY + index * lineHeight
            ctx.fillText(line, centerX, lineY)
          })
        }
      })

      // Ï∫îÎ≤ÑÏä§Î•º BlobÏúºÎ°ú Î≥ÄÌôò
      const blob = await new Promise<Blob>((resolve) => {
        canvas.toBlob((blob) => resolve(blob!), 'image/jpeg', 0.85)
      })

      // Cloudinary ÏóÖÎ°úÎìú
      const formData = new FormData()
      formData.append('image', blob, 'translated.jpg')
      formData.append('translateImage', 'false') // Ïù¥ÎØ∏ Î≤àÏó≠Îêú Ïù¥ÎØ∏ÏßÄ

      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData
      })

      const result = await response.json()
      
      if (result.success) {
        return result.imageUrl
      }
      return null
    } catch (error) {
      console.error('Generate translated image error:', error)
      return null
    }
  }

  // ÏôÑÎ£åÎêú Ïù¥ÎØ∏ÏßÄ ÏÉùÏÑ±
  const handleComplete = async () => {
    if (selections.length === 0) {
      return
    }

    if (selections.some(sel => sel.isTranslating)) {
      return
    }

    setIsTranslating(true)

    try {
      const translatedImageUrl = await generateTranslatedImage()
      if (translatedImageUrl) {
        setLastTranslatedImageUrl(translatedImageUrl)
        onTranslationComplete(translatedImageUrl)
      }
    } catch (error) {
      console.error('Complete error:', error)
    } finally {
      setIsTranslating(false)
    }
  }

  // ÏÑ†ÌÉù ÏòÅÏó≠ ÏÇ≠Ï†ú
  const handleClearSelections = () => {
    setSelections([])
    setCurrentSelection(null)
  }

  return (
    <div className="space-y-4">
      <div className="bg-white rounded-lg shadow-sm border">
        <div className="p-4">
          <div className="text-center space-y-4">
            <h3 className="text-lg font-semibold">Ïù¥ÎØ∏ÏßÄ Î≤àÏó≠ Ìé∏ÏßëÍ∏∞</h3>
            <div className="text-sm text-gray-600 space-y-1">
              <p>ÎßàÏö∞Ïä§Î°ú ÎìúÎûòÍ∑∏ÌïòÏó¨ Î≤àÏó≠Ìï† ÌÖçÏä§Ìä∏ ÏòÅÏó≠ÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî</p>
              <p className="text-xs">
                üí° <strong>ÌåÅ:</strong> ÏÑ†ÌÉùÎêú ÏòÅÏó≠ÏùÑ <span className="text-red-600">ÌÅ¥Î¶≠</span>ÌïòÍ±∞ÎÇò <span className="text-red-600">Ïö∞ÌÅ¥Î¶≠</span>ÌïòÎ©¥ Í∞úÎ≥Ñ ÏÇ≠Ï†úÎê©ÎãàÎã§
              </p>
            </div>
            
            <div className="flex justify-center">
              <div className="w-full">
                <canvas
                  ref={canvasRef}
                  onMouseDown={handleMouseDown}
                  onMouseMove={handleMouseMove}
                  onMouseUp={handleMouseUp}
                  onContextMenu={handleContextMenu}
                  className="cursor-crosshair block w-full border border-gray-300 rounded-lg"
                />
              </div>
            </div>
            
            <div className="flex gap-2 justify-center">
              <button
                onClick={handleClearSelections}
                disabled={selections.length === 0}
                className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                ÏÑ†ÌÉù Ï¥àÍ∏∞Ìôî
              </button>
              <button
                onClick={handleComplete}
                disabled={selections.length === 0 || isTranslating}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {isTranslating ? 'Ï≤òÎ¶¨ Ï§ë...' : 'Î≤àÏó≠ ÏôÑÎ£å'}
              </button>
              <button
                onClick={onCancel}
                className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 transition-colors"
              >
                Ï∑®ÏÜå
              </button>
            </div>
            
            {selections.length > 0 && (
              <div className="text-sm text-gray-600">
                ÏÑ†ÌÉùÎêú ÏòÅÏó≠: {selections.length}Í∞ú 
                {selections.filter(s => s.translatedText).length > 0 && 
                  ` (Î≤àÏó≠ ÏôÑÎ£å: ${selections.filter(s => s.translatedText).length}Í∞ú)`
                }
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
} 